.finish {
  width: 100%;
  height: 100%;
  background-image: url(../../static/media/child_bg.18fb80c1.png);
  background-repeat: no-repeat;
}
.finish .finish_body {
  width: 900px;
  position: relative;
  top: 50px;
  margin: 0 auto;
  height: 670px;
  background-color: #fff;
}
.finish .finish_body .finish_body_content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
}
.finish h3 {
  font-size: 30px;
  font-family: PingFang-SC-Bold;
  font-weight: bold;
  color: #333333;
}
.finish small {
  font-size: 20px;
  font-family: PingFang-SC-Bold;
  font-weight: bold;
  color: #999999;
}
