.record {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #000;
}
.record .record_header {
  text-align: center;
  height: 50px;
  width: 100%;
  background-color: #808080;
  position: absolute;
  color: #fff;
  font-size: 30px;
  font-family: PingFang-SC-Bold;
  font-weight: bold;
  color: #ffffff;
}
.record .record_header a {
  position: absolute;
  right: 20px;
}
.record .shot_btn {
  position: absolute;
  bottom: 100px;
  z-index: 99;
  width: 100%;
  display: flex;
  justify-content: center;
}
.record .shot_btn img {
  margin: 0 20px;
}
.record #video {
  position: absolute;
  top: 50px;
}
.record .photo_modal {
  width: 900px;
  height: 600px;
  position: relative;
  top: 70px;
  left: 180px;
  background-color: #fff;
}
.record .photo_modal .modal_body {
  display: flex;
  width: 600px;
  height: 600px;
  flex-direction: column;
  margin: 0 auto;
  align-items: center;
  justify-content: center;
}
.record .photo_modal .modal_body .btn_group {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 60px 0;
}
.record .photo_modal .modal_body .btn_group button {
  width: 70px;
  height: 40px;
}
.record .photo_modal .modal_body #canvas {
  width: 600px;
  height: auto;
}
