.detail {
  width: 900px;
  position: relative;
  top: 50px;
  margin: 0 auto;
  height: 670px;
  background-color: #fff;
}
.detail .confirm_body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 600px;
}
.detail .confirm_body .first {
  display: flex;
  justify-content: space-between;
  width: 400px;
}
.detail .confirm_body .first button {
  width: 124px;
  height: 56px;
  font-size: 28px;
  font-family: PingFang-SC-Bold;
  font-weight: bold;
  color: #ffffff;
}
.detail .confirm_body p {
  width: 400px;
  margin: 0;
  font-size: 28px;
  font-family: PingFang-SC-Bold;
  font-weight: bold;
  color: #333333;
}
.confirm {
  width: 100%;
  height: 100%;
  background-image: url(../../static/media/child_bg.18fb80c1.png);
  background-repeat: no-repeat;
}
