.users {
  background-image: url(../../static/media/child_bg.18fb80c1.png);
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
}
.users .users_body {
  width: 1100px;
  margin: 30px auto;
  display: flex;
  flex-wrap: wrap;
  height: 670px;
  position: relative;
}
.users .users_body .users_item {
  display: flex;
  width: 318px;
  flex-wrap: wrap;
  margin: 0 24px 0 24px;
  height: 260px;
}
.users .users_body .users_item .users_item_content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 10px;
  background-color: #fff;
}
.users .users_body .users_item .users_item_content h3 {
  margin: 0;
  font-size: 20px;
  font-family: PingFang-SC-Bold;
  font-weight: bold;
  color: #333333;
}
.users .users_body .users_item .users_item_content p {
  margin: 0;
  font-size: 15px;
  font-family: PingFang-SC-Bold;
  font-weight: bold;
  color: #666666;
}
.users .users_body .pagination {
  text-align: right;
  margin: 0;
  position: absolute;
  bottom: -18px;
}
