.header {
  text-align: center;
  position: relative;
  height: 70px;
  background-color: #fff;
}
.header img {
  position: absolute;
  top: 14px;
  right: 40px;
  width: 50px;
  height: 38px;
}
.header p {
  font-size: 40px;
  line-height: 70px;
  font-family: PingFang-SC-Heavy;
  font-weight: 800;
  color: #dd2d30;
  margin: 0;
}
