.journey {
  background-image: url(../../static/media/child_bg.18fb80c1.png);
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
}
.journey .journey_body {
  display: flex;
  flex-wrap: wrap;
  margin: 15px 0;
  justify-content: center;
}
.journey .journey_body .journey_item {
  width: 480px;
  height: 300px;
  background-color: rgba(255, 255, 255, 0.8);
  margin: 25px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.journey .journey_body .journey_item p {
  font-size: 48px;
  font-family: PingFang-SC-Bold;
  font-weight: bold;
  color: #f74043;
  margin: 0;
}
.journey .table {
  width: 1000px;
  margin: 0 auto;
  max-height: 730px;
  overflow: hidden;
}
.journey .table .table-item {
  width: 100%;
  height: 200px;
  margin: 20px 0;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
}
.journey .table .table-item .table-item-content {
  width: 670px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.journey .table .table-item .table-item-content .btn_wrap {
  width: 100%;
  text-align: right;
  align-self: flex-end;
}
.journey .table .pagination {
  text-align: right;
  margin: 0;
}
